import React from 'react';
import { useStaticQuery, graphql } from 'gatsby';
import '../global.css';

export const Footer = () => {
  const footerData = useStaticQuery(graphql`
    query FooterQuey {
      avatar: file(absolutePath: { regex: "/profile-pic.jpg/" }) {
        childImageSharp {
          fixed(width: 50, height: 50) {
            ...GatsbyImageSharpFixed
          }
        }
      }
      site {
        siteMetadata {
          author
          social {
            github
            linkedin
          }
        }
      }
    }
  `);
  // Get the 'social' object from site metadata in query.
  const { social } = footerData.site.siteMetadata;
  // URL for GitHub
  const githubLink = `https://github.com/${social.github}`;
  // URL for LinkedIn
  const linkedinLink = `https://linkedin.com/in/${social.linkedin}`;
  return (
    <footer>
      <a href={githubLink} target="_blank" rel="noopener noreferrer">
        Github
      </a>
      <a href={linkedinLink} target="_blank" rel="noopener noreferrer">
        LinkedIn
      </a>
      <span style={{ fontFamily: `inherit`, fontWeight: `500` }}>
        &copy; 2020
      </span>
      <img
        src={require(`../../content/assets/mobile_logo.png`)}
        alt={`Andrew Stolzle logo`}
        style={{ height: `75px`, margin: `0 5px` }}
      />
    </footer>
  );
};

export default Footer;
