import React from 'react';
import Button from '@material-ui/core/Button';
import { ThemeProvider } from '@material-ui/styles';
import { useStaticQuery, graphql } from 'gatsby';
import { theme } from '../utils/theme';
import Pdf from '../../content/assets/stolzle_resume.pdf';
import '../global.css';

const Bio = () => {
  const data = useStaticQuery(graphql`
    query BioQuery {
      avatar: file(absolutePath: { regex: "/profile-pic.jpg/" }) {
        childImageSharp {
          fixed(width: 50, height: 50) {
            ...GatsbyImageSharpFixed
          }
        }
      }
      site {
        siteMetadata {
          author
          social {
            github
            linkedin
          }
        }
      }
    }
  `);
  const { social } = data.site.siteMetadata;
  // URL for GitHub
  const githubLink = `https://github.com/${social.github}`;
  // URL for LinkedIn
  const linkedinLink = `https://linkedin.com/in/${social.linkedin}`;
  return (
    <div
      style={{
        display: `flex`,
        flexDirection: `column`,
        alignItems: `center`,
      }}
    >
      <p>
        <span className={`bio-body`}>
          Hey there{' '}
          <span role={'img'} aria-label={'hand wave emoji'}>
            
          </span>{' '}
          I'm Andrew. Developer with 2 years experience creating primarily web
          and mobile applications. I've worked with Angular, React,
          Xamarin, Android Studio, .NET Core, and other related tools.
        </span>
      </p>
      <div style={{ display: `flex`, width: `100%` }}>
        {' '}
        <div
          style={{
            display: `flex`,
            flexDirection: `column`,
            alignItems: `center`,
            flexWrap: `wrap`,
            width: `100%`,
          }}
        >
          <ThemeProvider theme={theme}>
            <div className={`social-buttons`}>
              <div>
                <Button
                  style={{ color: `#212121`, fontWeight: `bold` }}
                  variant="contained"
                  color="primary"
                  onClick={() => {
                    window.open(Pdf, '_blank', 'noopener noreferrer');
                  }}
                >
                  Resume
                </Button>
              </div>
              <div>
                <Button
                  style={{ color: `#212121`, fontWeight: `bold` }}
                  variant="contained"
                  color="secondary"
                  onClick={() => {
                    window.open(githubLink, '_blank', 'noopener noreferrer');
                  }}
                >
                  GitHub
                </Button>
              </div>
              <div>
                <Button
                  variant="outlined"
                  color="primary"
                  onClick={() => {
                    window.open(linkedinLink, '_blank', 'noopener noreferrer');
                  }}
                >
                  LinkedIn
                </Button>
              </div>
              <div>
                <Button
                  variant="outlined"
                  color="secondary"
                  onClick={() => {
                    window.location = 'mailto:andrewstolzle@gmail.com';
                  }}
                >
                  Email
                </Button>
              </div>
            </div>
          </ThemeProvider>
        </div>
      </div>
    </div>
  );
};

export default Bio;
